<template>
  <div id="adoutUs">
    <head-vue title="关于我们" goBack="true"></head-vue>
    <div class="top">
      <img src="../../assets/me/logo@3x.png" alt="">
      <span>XXXXX有限公司</span>
    </div>
    <ul class="menu">
      <li>
        <div class="left">
          <img src="../../assets/me/wechat2@3x.png" alt="">
          <span>微信服务号</span>
        </div>
        <div class="right">
          XXXXXXX
        </div>
      </li>
      <li>
        <div class="left">
          <img src="../../assets/me/phone@3x.png" alt="">
          <span>联系电话</span>
        </div>
        <div class="right">
          020-888888888
        </div>
      </li>
      <li>
        <div class="left">
          <img src="../../assets/me/website@3x.png" alt="">
          <span>官方网站</span>
        </div>
        <div class="right">
          www.xxxx.com.cn
        </div>
      </li>
      <li>
        <div class="left">
          <img src="../../assets/me/location@3x.png" alt="">
          <span>公司地址</span>
        </div>
        <div class="right">
          xx市xxxx区xx路xxx号
        </div>
      </li>
    </ul>
  </div>
</template>
<script>
import head from '../../components/common/head'

import {
  versionNumberRequest
} from '../../api/method'
import {
  versionNumberResponse
} from '../../api/getData'
export default {
  data () {
    return {
      version: null
    }
  },
  components: {
    'head-vue': head
  },
  created () {
    versionNumberRequest(this.NAVIGATOR, this)
    versionNumberResponse(this.NAVIGATOR, this)
  }
}
</script>
<style lang="less" scoped>
#adoutUs{
  .fs;
  z-index: 999;
  background: white;
  .wh(100%, 100%);
  display: flex;
  flex-direction: column;
  color: #2b2b2b;
  font-size: 30px;
  .top{
    display: flex;
    width: 100%;
    padding-top: 99px;
    flex-direction: column;
    font-size: 30px;
    span{
      align-self: center;
      margin-top: 54px;
    }
    img{
      .wh(171px, 54px);
      align-self: center;
    }
  }
  ul{
    display: flex;
    flex-direction: column;
    border-bottom:  1px solid #e6e5e5;
    margin-top: 103px;
    li{
      .wh(100%, 137px);
      display: flex;
      justify-content: space-between;
      padding: 0 30px;
      border-top: 1px solid #e6e5e5;
    }
    .left{
      height: 100%;
      display: flex;
      flex: 1;
      width: 40%;
      img{
        .wh(44px, 44px);
        align-self: center;
        margin-right: 18px;
      }
    }
    .right{
      flex: 1;
      align-self: center;
      text-align: right;
    }
    span{
      font-size: 30px;
      align-self: centr;
      line-height: 130px;

    }
  }
}
</style>



